.page {
  scroll-behavior: smooth;
  background: #f2f6fa;
  overflow: hidden scroll;
}

a {
  cursor: pointer;
}

.container {
  padding-inline: 20px;
  flex-direction: column;
  gap: 0 48px;
  padding-top: 20px;
  display: flex;
}

@media (width >= 640px) {
  .container {
    padding-inline: 41px;
  }
}

@media (width >= 1024px) {
  .container {
    padding-top: 48px;
    padding-inline: 54px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1200px;
    margin-inline: auto;
  }
}

.container--main {
  padding-top: 120px;
  padding-inline: 20px;
  padding-bottom: 0;
}

@media (width >= 640px) {
