    margin-bottom: 0;
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  background-color: #2c2c2c;
  border: none;
  border-radius: 0 0 30px 30px;
