}

.top-bar__logo:hover {
  transform: scale(1.1);
}

.icon {
  width: 18px;
  height: 8px;
  display: inline-block;
}

.icon--menu {
  cursor: pointer;
  background-image: url("Menu.fb41b0c6.png");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 12px;
  display: block;
}

@media (width >= 1024px) {
  .icon--menu {
    display: none;
  }
}

.icon--close {
  cursor: pointer;
  background-image: url("close.69c2036b.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 18px;
  margin-top: 7px;
  display: block;
}

@media (width >= 1024px) {
  .icon--close {
    display: none;
  }
}

.nav__list {
  justify-content: center;
  gap: 48px;
  padding-top: 6px;
  list-style-type: none;
  display: none;
}

@media (width >= 1024px) {
  .nav__list {
    display: flex;
  }
}

.nav__item {
  transition: transform .3s;
}

.nav__item:hover {
  transform: scale(1.1);
}

.nav__item:after {
  content: "";
  background: linear-gradient(90deg, #5b84e1, #2060f6);
  width: 100%;
  height: 2px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.nav__item:hover:after {
  transform: scale(1);
}

.nav__link {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
  text-decoration: none;
}

.hiring {
  text-decoration: none;
  display: flex;
}

.hiring__hire-us {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-block: 0;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
  transition: transform .3s;
  display: flex;
  position: relative;
}

.hiring__hire-us:hover {
  transform: scale(1.1);
}

@media (width >= 1024px) {
  .hiring__hire-us {
