  }
}

.hiring__hire-us:after {
  content: "";
  background: linear-gradient(90deg, #5b84e1, #2060f6);
  border-radius: 4px;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: -8px;
  left: 0;
}

@media (width >= 640px) {
  .hiring__hire-us:after {
    bottom: -8px;
  }
}

@media (width >= 1280px) {
  .hiring__hire-us:after {
    bottom: -8px;
  }
}

.menu {
